wAnimate.add({'width': 600,'height':600, 'top': 80}, 1000, 'linear', function(){
	console.log('animate done!');
});
wAnimate.add({'height': 100, 'left':200, 'opacity': 0}, 2000, 'easeIn');

require('./css/style.scss');


